import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Select, Table, Form, Spin, message } from 'antd';
import {
  getHeadNoInTPByTPId,
  saveTestPackage,
  lockTestPackage,
  exportExportTPById,
  exportExportExcelTPById,
} from 'services/testPackage';
import { getAllGradeNote } from 'services/gradeNote';
import { InputNumberScore10 } from 'components/common';
import { BtnExport, BtnSave, BtnExportPdf, BtnLock, Btn } from 'components/common/button';
import to from 'await-to-js';
// import { insertScoreBag } from 'dump/data';
import CreditClass from './CreditClass';

const { Option } = Select;
@Form.create({ name: 'formTeacherNote' })
class InsertScoreBagPage extends Component {
  state = {
    paramSearch: {},
    dataGradeNote: [],
    insertScoreBag: {},
    loadingPage: false,
    isLocked: false,
  };

  componentDidMount() {
    this.getDataGradeNote();
  }

  getDataTable = async value => {
    this.setState({
      loading: true,
    });
    const [error, response] = await to(getHeadNoInTPByTPId({ tPId: value.examBag }));
    this.setState({
      loading: false,
    });
    if (error) return;
    const insertScoreBag = {};

    const isLocked = response.data.items.some(item => item.isLocked === true);
    response.data.items.forEach((item, index) => {
      const dataItem = {
        key: index,
        ...item,
      };
      insertScoreBag[index] = dataItem;
    });
    this.setState({
      insertScoreBag,
      isLocked,
    });
  };

  getDataGradeNote = async () => {
    const [error, response] = await to(getAllGradeNote());
    if (error) return;
    this.setState({
      dataGradeNote: response.data.items,
    });
  };
  dataForm = value => {
    this.setState({
      paramSearch: value,
    });
    this.getDataTable(value);
  };

  changeInput = (e, record) => {
    const { insertScoreBag } = this.state;
    const dataTable = insertScoreBag;
    const dataEdit = {
      ...record,
      grade: parseFloat(e.target.value),
    };
    dataTable[record.key] = dataEdit;

    this.setState({
      insertScoreBag: dataTable,
    });
  };

  changeGradeNoteId = (record, index, value) => {
    const { insertScoreBag } = this.state;
    const dataTable = insertScoreBag;
    const dataEdit = {
      ...record,
      gradeNoteId: value,
    };
    dataTable[index] = dataEdit;
    this.setState({
      insertScoreBag: dataTable,
    });
  };

  saveListStudent = async () => {
    const { insertScoreBag, paramSearch } = this.state;
    const details = Object.values(insertScoreBag).map(item => {
      const dataFormat = {
        detachHeadNo: item.detachHeadNo,
        grade: item.grade,
        gradeNoteId: item.gradeNoteId,
      };
      return dataFormat;
    });
    const dataUpdate = {
      tpId: paramSearch.examBag,
      details,
    };
    this.setState({ loadingPage: true });
    const [error] = await to(saveTestPackage(dataUpdate));
    this.setState({ loadingPage: false });
    if (error) return;
    message.success('Lưu thông tin thành công');
  };

  lockData = async () => {
    const { paramSearch, isLocked } = this.state;
    const dataUpdate = {
      tpId: paramSearch.examBag,
      isLocked: !isLocked,
    };
    this.setState({ loadingPage: true });
    const [error] = await to(lockTestPackage(dataUpdate));
    this.setState({ loadingPage: false });
    if (error) return;
    this.setState({
      isLocked: !isLocked,
    });
    message.success('Thao tác thành công');
  };

  exportExcel = async () => {
    const { paramSearch } = this.state;
    const dataUpdate = {
      tpId: paramSearch.examBag,
    };
    this.setState({ loadingPage: true });
    await to(exportExportExcelTPById(dataUpdate));
    this.setState({ loadingPage: false });
  };
  exportPdf = async () => {
    const { paramSearch } = this.state;
    const dataUpdate = {
      tpId: paramSearch.examBag,
    };
    this.setState({ loadingPage: true });
    await to(exportExportTPById(dataUpdate));
    this.setState({ loadingPage: false });
  };

  render() {
    const { loading, insertScoreBag, loadingPage, dataGradeNote, isLocked } = this.state;
    const { form } = this.props;
    const columns = [
      {
        title: 'Số phách',
        dataIndex: 'detachHeadNo',
        key: 'detachHeadNo',
        width: '30%',
      },
      {
        title: 'Thi (60)',
        dataIndex: 'grade',
        key: 'grade',
        width: '35%',
        render: (text, record, index) => (
          <Form.Item>
            {form.getFieldDecorator(`grade-${index}`, {
              initialValue: record.grade,
            })(
              <InputNumberScore10
                disabled={isLocked}
                onBlur={e => this.changeInput(e, record)}
                style={{ width: '100%' }}
              />
            )}
          </Form.Item>
        ),
      },

      {
        title: 'Ghi chú',
        dataIndex: 'note',
        width: '35%',
        render: (text, record, index) => (
          <Form.Item>
            {form.getFieldDecorator(`gradeNoteId-${index}`, {
              initialValue: record.gradeNoteId,
            })(
              <Select
                disabled={isLocked}
                style={{ width: '100%' }}
                onChange={value => this.changeGradeNoteId(record, index, value)}
              >
                {dataGradeNote.map(note => (
                  <Option value={note.id}>{note.label}</Option>
                ))}
              </Select>
            )}
          </Form.Item>
        ),
      },
    ];
    return (
      <Card title="Nhập điểm theo túi thi">
        <Spin spinning={loadingPage} size="large">
          <Row>
            <Col span={24}>
              <CreditClass dataForm={this.dataForm} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                loading={loading}
                bordered
                dataSource={Object.values(insertScoreBag)}
                columns={columns}
                rowKey="key"
              />
            </Col>
          </Row>
          <Row>
            {Object.values(insertScoreBag).length > 0 && (
              <Col span={24} style={{ textAlign: 'center' }}>
                <BtnSave onClick={() => this.saveListStudent()} />
                <BtnExport
                  style={{ marginLeft: 10, marginRight: 10 }}
                  onClick={() => this.exportExcel()}
                />
                <BtnExportPdf onClick={() => this.exportPdf()} />{' '}
                {isLocked ? (
                  <Btn title="Mở khóa" onClick={() => this.lockData()} />
                ) : (
                  <BtnLock style={{ marginLeft: 10 }} onClick={() => this.lockData()} />
                )}
              </Col>
            )}
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScoreBagPage />
  </Page>
);



