import { request, requestDownload } from 'utils';
// import qs from 'qs';

const prefix = '/api/education';

export const getTestPackages = ({ page, pageSize=10, orderBy, isPagingEnabled=true } = {}) =>
  request({
    prefix,
    url: '/TestPackage',
    method: 'GET',
    params: {page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });

 export const getStudentInTestPackage = ({
  packageNo,
  tOId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/TestPackage/GetStudentInTP',
    method: 'GET',
    params: {packageNo,tOId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });


  export const createTestPackage = ({
    toId,
    packageNo,
    headSignal,
    fromNo,
    students
  } = {}) =>
    request({
      prefix,
      url: '/TestPackage',
      method: 'POST',
      params: {},
      data: {
        toId,
        packageNo,
        headSignal,
        fromNo,
        students
      }
    });


export const deleteTestPackage = ({toId,packageNo} = {}) =>
  request({
    prefix,
    url: '/TestPackage',
    method: 'DELETE',
    params: {},
    data: {toId,packageNo}
  });

export const getExamBag = ({
  yearEduId,
  semesterId,
  trainTypeId,
  facultyId,
  examBatch,
  examTime,
  SubjectId,
  isPagingEnabled = false,
} = {}) => {
  return request({
    prefix,
    url: '/TestPackage/GetTestPackages',
    method: 'GET',
    params: {
      yearEduId,
      semesterId,
      trainTypeId,
      SubjectId,
      facultyId,
      examBatch,
      examTime,
      isPagingEnabled,
    },
  });
};

export const getHeadNoInTPByTPId = ({ tPId } = {}) =>
  request({
    prefix,
    url: '/TestPackage/GetHeadNoInTPByTPId',
    method: 'GET',
    params: {
      tPId,
      isPagingEnabled: false,
    },
  });

export const saveTestPackage = dataStudent =>
  request({
    prefix,
    url: '/TestPackage/UpdateGradeByTP',
    method: 'PUT',
    data: dataStudent,
  });

export const lockTestPackage = dataStudent =>
  request({
    prefix,
    url: '/TestPackage/SetLockedByTPId',
    method: 'PUT',
    data: dataStudent,
  });

export const exportExportExcelTPById = paramsSearch =>
  requestDownload({
    defaultFileName: `ExportExcelTPById.xlsx`,
    prefix,
    url: '/TestPackage/ExportExcelTPById',
    method: 'POST',
    data: paramsSearch,
  });

export const exportExportTPById = paramsSearch =>
  requestDownload({
    defaultFileName: `ExportTPById.pdf`,
    prefix,
    url: '/TestPackage/ExportTPById',
    method: 'POST',
    data: paramsSearch,
  });

  export const createDetachHeadNoAuto = data =>
    request({
      prefix,
      url: '/TestPackage/DetachHeadNoAuto',
      method: 'POST',
      params: {},
      data
    });



