import React, { Component } from 'react';
import { Col, Form, Row, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getExamBag } from 'services/testPackage';
import { getListSubject } from 'services/Subject';
import { getFacultys } from 'services/faculty';
// import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

const { Option } = Select;

@Form.create({ name: 'formTeacher' })
class CreditClass extends Component {
  state = {};
  componentDidMount = () => {
    // const { form } = this.props;
    // form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) return;
      this.props.dataForm(values);
      // updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
      SubjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
      examTime: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
      examBag: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
      examBatch: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };

    const {
      examTime,
      facultyId,
      semesterId,
      SubjectId,
      trainTypeId,
      yearEduId,
      examBatch,
    } = this.props.form.getFieldsValue();

    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={6}
          />

          <Col span={6}>
            <Form.Item label="Hệ">
              {getFieldDecorator(
                'trainTypeId',
                optionFieldDecorator.trainTypeId
              )(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator(
                'facultyId',
                optionFieldDecorator.facultyId
              )(
                <SelectAjax
                  // setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  // params={{ trainTypeId }}
                  // allowGetData={!!trainTypeId}
                  // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
          </Col>

          <Col span={6}>
            <Form.Item label="Môn học">
              {getFieldDecorator(
                'SubjectId',
                optionFieldDecorator.SubjectId
              )(
                <SelectAjax
                  // setFieldsValue={setFieldsValue}
                  service={getListSubject}
                  cga
                  // params={{ trainTypeId }}
                  // allowGetData={!!trainTypeId}
                  // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Lần thi">
              {getFieldDecorator(
                'examTime',
                optionFieldDecorator.examTime
              )(
                <Select>
                  <Option value={1}>01</Option>
                  <Option value={2}>02</Option>
                  <Option value={3}>03</Option>
                  <Option value={4}>04</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator(
                'examBatch',
                optionFieldDecorator.examBatch
              )(
                <Select>
                  <Option value={1}>01</Option>
                  <Option value={2}>02</Option>
                  <Option value={3}>03</Option>
                  <Option value={4}>04</Option>
                </Select>
              )}
            </Form.Item>
          </Col>

          <Col span={6}>
            <Form.Item label="Túi thi">
              {getFieldDecorator(
                'examBag',
                optionFieldDecorator.examBag
              )(
                <SelectAjax
                  // setFieldsValue={setFieldsValue}
                  service={getExamBag}
                  params={{
                    examBatch,
                    examTime,
                    facultyId,
                    semesterId,
                    SubjectId,
                    trainTypeId,
                    yearEduId,
                  }}
                  keyValue="value"
                  allowGetData={!!examBatch}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={() => this.handleReset()} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default CreditClass;



